@keyframes rotate-minute {
  from {
    transform: translate(-50%)rotate(0);
  }

  to {
    transform: translate(-50%)rotate(360deg);
  }
}

@keyframes rotate-second {
  from {
    transform: translate(-50%, -47.5%)rotate(0);
  }

  to {
    transform: translate(-50%, -47.5%)rotate(360deg);
  }
}

* {
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch--speed-up :first-child {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 600s steps(60, end) infinite rotate-minute;
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%)rotate(0);
}

.stopwatch--speed-up :nth-child(2) {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 10s linear infinite rotate-second;
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -47.5%)rotate(0);
}

.stopwatch__minute {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-minute;
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%)rotate(0);
}

.stopwatch__second {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-second;
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -47.5%)rotate(0);
}

.stopwatch__dot {
  transform-origin: center;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)rotate(0);
}
