@keyframes rotate-minute {
  from {
    transform: translate(-50%, 0) rotate(0);
  }

  to {
    transform: translate(-50%, 0) rotate(360deg);
  }
}

@keyframes rotate-second {
  from {
    transform: translate(-50%, -47.5%) rotate(0);
  }

  to {
    transform: translate(-50%, -47.5%) rotate(360deg);
  }
}
